.main-layout {
  min-height: 100vh !important;
}

.header .logo {
  margin-right: 20px;
  width: 120px;
  float: left;
}

.header .logo img {
  width: inherit;
}

.content-layout-background {
  padding: 24px;
  margin: 0;
  background: #fff;
}

.header .header-actions-menu {
  float: left;
}

.header .header-auth-menu {
  float: right;
}

.header .logout-btn {
  color: lightgrey;
  display: flex;
  float: right;
  cursor: pointer;
  user-select: none;
  transition: ease .3s;
}

.header .logout-btn:hover {
  color: #1890ff;
}

@media(max-width: 576px) {
  .ant-layout-header {
    padding: 0 5px !important;
  }

  .logo {
    margin: 0 !important;
  }
}

@media(max-width: 787px) {
  .header .logout-btn {
    display: none;
  }
}
